import React from "react";
import "./Faqs.scss";

function Faqs() {
  return (
    <>
      <section className="Faqs top">
        <section className="looking">
          <div className="container">
            <div className="heading heading-yellow heading-center top">
              <h1>FAQs</h1>
            </div>
            <div className="row top">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left">
                <h5>What is SMART MOVE?</h5>
                <p>
                  SMART MOVE is a global competition regarding proposing data
                  driven solutions to address mobility challenges faced by
                  Indian cities. The data challenge is outlined considering the
                  Government of India’s efforts to become Aatma Nirbhar
                  (self-reliant) and help cities to transform into a digitally
                  empowered society.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Who is organizing SMART MOVE?</h5>
                <p>
                  GIZ is supporting the Ministry of Housing and Urban Affairs
                  (MoHUA) in organizing SMART MOVE data challenge with National
                  Institute of Urban Affairs (NIUA) as the implementation
                  partner.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>When is the last date of registration?</h5>
                <p>
                  The last date of registration is 22 December 2020. Though we
                  highly suggest registering in advance so that you have
                  sufficient time to carefully fill the concept note proposal.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>When is the last date of Concept Note submission?</h5>
                <p>
                  The last date of concept note submission is 22 December 2020.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What is the Eligibility criteria?</h5>
                <p>
                  The data challenge is open to all undergraduate and
                  postgraduate students and researchers globally. Please refer
                  the Guidelines{" "}
                  <a
                    href="/Smartmove_DataChallengeGuidelines.pdf"
                    target="_blank"
                  >
                    here
                  </a>{" "}
                  to learn about the eligibility criteria.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What is the guidance on team composition ?</h5>
                <p>
                  Teams must have no more than four members with at least one
                  team member of Indian nationality who understands the local
                  context of Indian mobility sector.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>How many ideas/entries can a team submit?</h5>
                <p>
                  A team can submit a maximum of one idea in each of the three
                  themes. However, there shall not be any repetition of proposal
                  within the same theme. A unique email address should be used
                  for each application.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Can I participate for more than one team?</h5>
                <p>
                  No, participants cannot be a part of more than one team. This
                  is to avoid unnecessary conflict of interest amongst
                  participating teams.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Should my idea be related to the three main themes?</h5>
                <p>
                  Any innovative idea that will help improving the accessibility
                  and mobility of people and goods by unlocking the potential of
                  urban mobility datasets are welcomed, but it is mandatory that
                  the participants identify the theme and sub theme that matches
                  the most with their proposed solution.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Should my idea be related to the three main themes?</h5>
                <p>
                  Any innovative idea that will help improving the accessibility
                  and mobility of people and goods by unlocking the potential of
                  urban mobility datasets are welcomed, but it is mandatory that
                  the participants identify the theme and sub theme that matches
                  the most with their proposed solution.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>
                  Should my idea be fully developed before submitting the
                  concept note?
                </h5>
                <p>
                  No, the idea need not be fully developed while submitting the
                  concept note but it should have a strong potential of
                  scalability and implementability by the end of the challenge.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What are the various stages of participation ?</h5>
                <p>The challenge shall be undertaken over the three phases:</p>
                <p>Phase1: Submission of concept</p>
                <p>Phase 2: Matchmaking with the Cities</p>
                <p>Phase 3: Submission for final solution</p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Is there any standard template for submission ?</h5>
                <p>
                  A submission template for concept note stage and final
                  solution stage shall be provided by the organizing team.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What is the review process and evaluation criteria?</h5>
                <p>
                  The process and criteria for evaluation is defined in the
                  challenge guidelines. Please refer to the Guidelines{" "}
                  <a
                    href="/Smartmove_DataChallengeGuidelines.pdf"
                    target="_blank"
                  >
                    here
                  </a>{" "}
                  to learn in depth about the evaluation criteria.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What does partnering with the cities mean?</h5>
                <p>
                  The shortlisted concept note shall be circulated to the
                  partner cities of the challenge. Partner Cities are the one
                  who are supporting this challenge by providing the mobility
                  datasets. Each of the shortlisted proposals shall be matched
                  with one or more interested cities. Participants will get a
                  chance to pitch their solution to the interested cities. These
                  cities shall also assist participants during the mentorship
                  stage to tailor and develop their solution.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>
                  What kind of support is being offered during the mentorship
                  phase?
                </h5>
                <p>
                  Experts from academia, industry, start-ups and officials from
                  Ministry and participating cities will assist the teams with
                  development of solution using the mobility data for improving,
                  elaborating, and creating POC along with the business model.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>What is the review process and evaluation criteria?</h5>
                <p>
                  The process and criteria for evaluation is defined in the
                  challenge guidelines. Please refer to the Guidelines{" "}
                  <a
                    href="/Smartmove_DataChallengeGuidelines.pdf"
                    target="_blank"
                  >
                    here
                  </a>{" "}
                  to learn in depth about the evaluation criteria.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Who decides the winners?</h5>
                <p>
                  A jury that comprises of national and international experts
                  will be judging the competition.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>
                  What happens to the teams that don’t make it to the final
                  stage of the Challenge?
                </h5>
                <p>
                  All selected participants whose concept proposal passed the
                  first phase evaluation will get a certificate of
                  participation. For details of winning award and other
                  incentives please refer to the Challenge Guidelines{" "}
                  <a
                    href="/Smartmove_DataChallengeGuidelines.pdf"
                    target="_blank"
                  >
                    here
                  </a>
                  .
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>
                  Once the Challenge has ended, then what is expected from the
                  winners?
                </h5>
                <p>
                  This is not an open ideas competition, implementability is key
                  throughout the challenge process. The winning team shall be
                  invited to present their solution to Smart Cities Mission and
                  if the solution is unique and has potential for implementation
                  in multiple cities then your team will have an opportunity
                  (outside the SMART MOVE challenge) to develop a full
                  commercial product.
                </p>
              </div>
            </div>

            <div className="row">
              <div className="col-md-1"></div>
              <div className="col-md-10 align-left top2">
                <h5>Who retains the intellectual property for my idea?</h5>
                <p>
                  All participants hold the rights to their ideas and data they
                  bring – they can choose to share their data for future use by
                  others through open sources on the data portal set for the
                  challenge
                </p>
              </div>
            </div>
          </div>
        </section>
      </section>
    </>
  );
}

export default Faqs;
